#nav {
  display: flex;
  margin-bottom: $nav_padding-bottom;
  // align items by the baseline of text
  align-items: baseline;
  // make nav scrollable on small screens
  overflow: auto;

  &__brand {
    margin: 0;
    margin-right: auto;
    text-transform: none;
  }
}

.nav__link {
  margin-left: $nav__link_spacing;

  transition: color $color_transition;

  &:hover {
    color: $yellow;

    &::after {
      width: 100%;
      margin-left: 0;
    }
  }

  // add underline for links
  &::after {
    display: block;
    content: '';
    width: 0;
    height: $nav__link_underline-height;
    margin-top: $nav__link_underline-margin-top;
    margin-left: auto;
    background-color: $yellow;

    transition: width $color_transition, margin-left $color_transition;
  }
}

#nav__link_icon-search {
  margin-left: $nav__link_icon_spacing;
}
